.nav {
    @media #{$mediaMobile}{
        @include flex;
        width: 100%;
        height: 84px;
        background: $white;
        position: absolute;
        top: 59px;
        left: 0;
        opacity: 0;
        @include transition;
        pointer-events: none;
        z-index: 3000;
        &.active {
            pointer-events: all;
            opacity: 1;
        }
    }
    &__list {
        @include flex(flex-start,center,wrap);
        padding-left: 0;
        @media #{$mediaMobile}{
            width: 100%;
            padding: 10px;
            margin: 10px 5px;
            text-align: center;
        }
        &__item {
            @media #{$mediaMobile}{
                width: 100%;
            }
        }
        &__link {
            display: inline-block;
            padding-right: 64px;
            font-family: "dinAlternateBold", Arial, Helvetica, sans-serif;
            color: $black;
            &.active {
                font-family: "dinProBlack", Arial, Helvetica, sans-serif;
            }
            @media #{$mediaMobile} {
                width: 100%;
                padding-right: 0;
                margin-bottom: 5px;
                text-align: center;
            }
        }
    }
}