label.btn {
	input {
		display: none !important;
	}
}

.btn {
	@include flex(center,center,nowrap,inline-flex);
	min-width: 180px;
	padding: 13px 15px;
	vertical-align: middle;
	border: 1px solid transparent;
	border-radius: 3px;
	outline: none;
	background: $lBlue;
	font-family: "dinLight", Arial, Helvetica, sans-serif;
	@include font-size(16);
	text-align: center;
	color: $white;
	line-height: 1;
	cursor: pointer;
	@include transition;
	&:hover {
		background: $white;
		color: $lBlue;
	}
	&--white {
		border-color: $white;
		background: $white;
		color: $lBlack;
		&:hover {
			background: $lBlack;
			color: $white;
		}
	}
}

.btnNav {
	display: none;
	width: 45px;
	height: 24px;
	margin-right: 10px;
	&.active {
		.btnNav__line {
			&:first-of-type {
				transform: translateY(10px) rotate(35deg);
			}
			&:nth-of-type(2) {
				opacity: 0;
			}
			&:last-of-type {
				transform: translateY(-10px) rotate(-35deg);
			}
		}
	}
	@media #{$mediaMobile}{
		@include flex(center,stretch,wrap);
	}
	&__line {
		display: block;
		width: 100%;
		height: 4px;
		border-radius: 5px;
		background: $white;
		cursor: pointer;
		@include transition;
		&:first-of-type {
			align-self: flex-start;
		}
		&:nth-of-type(2) {
			align-self: center;
		}
		&:last-of-type {
			align-self: flex-end;
		}
	}
}