.carousel {
    padding-top: 40px;
    padding-bottom: 70px;
    .owl-item {
        text-align: center;
    }
    &__item {
        display: inline-block;
        width: auto;
        height: 500px;
    }
    &__img {
        width: auto;
        height: 100%;
    }
    &__nav {
        @include flex;
        width: 100%;
        height: 0;
        margin: auto;
        position: absolute;
        top: 0;
        bottom: 0;
        pointer-events: none;
        z-index: +1;
    }
    &__prev, &__next {
        display: inline-block;
        width: 20px;
        height: 35px;
        background: url("../images/icons/arrow.png") no-repeat center;
        background-size: contain;
        position: absolute;
        top: -20px;
        left: -10px;
        transform: rotate(180deg);
        pointer-events: all;
        cursor: pointer;
        @media #{$mediaMobile}{
            left: 0;
        }
    }
    &__next {
        left: unset;
        right: -10px;
        transform: rotate(0);
        @media #{$mediaMobile}{
            right: 0;
        }
    }
}

.owl-nav {
    display: none;
}

.owl-dots {
    display: none;
    @media #{$mediaMobile}{
        display: block;
    }
}