.banner {
    min-height: 700px;
    margin-top: 140px;
    background-color: $lGray;
    background-image: url("../images/bg/bg-banner.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    @media #{$mediaMobile}{
        background-image: none;
    }
    &__container {
        @include flex(flex-end,center,wrap);
        height: 100%;
        min-height: inherit;
        @media #{$mediaMobile}{
            @include flex(center,flex-start,wrap);
        }
    }
    &__img {
        display: none;
        margin-right: auto;
        @media #{$mediaMobile}{
            display: block;
            margin-left: auto;
            margin-right: unset;
        }
    }
    &__left {
        margin-right: auto;
        @media #{$mediaMobile}{
            order: 1;
            width: 100%;
        }
    }
    &__right {
        @media #{$mediaMobile}{
            width: 100%;
        }
    }
}