.modal {
	@include flex;
	width: 100vw;
	height: 100vh;
	background: rgba($black,.9);
	position: fixed;
	top: 0;
	left: 0;
	@include transition;
	z-index: 3000;
	&__container {
		@include flex(center,center,wrap);
		width: 100%;
		max-width: $maxWModal;
		height: auto;
		min-height: $minHModal;
		padding: 10px;
		text-align: center;
	}
	&__close {
		@include flex;
		width: 40px;
		height: 40px;
		padding-bottom: 6px;
		border-radius: 50%;
		background: $white;
		@include font-size(26);
		line-height: 0;
		position: absolute;
		top: -20px;
		right: -20px;
		cursor: pointer;
		@media #{$mediaMobile}{
			top: -9px;
			right: -9px;
		}
	}
	&__title {
		width: 100%;
		max-width: 300px;
		margin-bottom: 40px;
		@include font-size(15);
		text-align: center;
		font-weight: normal;
		color: $white;
		line-height: 20px;
	}
	&__desc {
		@include font-size(15);
	}
}