.container {
    width: 100%;
    &--general {
        background: $white;
        overflow: hidden;
    }
    &--limit {
        max-width: 1200px;
        padding-left: 10px;
        padding-right: 10px;
        margin-left: auto;
        margin-right: auto;
    }
    &--gray {
        background: $lGray;
    }
    &--portability {
        @include flex(flex-start,center);
        min-height: 410px;
        background: url("../images/bg/bg-portability.jpg") no-repeat center;
        background-size: cover;
    }
    &--icons {
        min-height: 360px;
    }
}