form {
	input[type='text'],
	input[type='password'],
	input[type='date'],
	input[type='phone'],
	input[type='number'],
	input[type='email']{
		@include placeholder {
			font-family: "dinMedium", Arial, Helvetica, sans-serif;
			@include font-size(14);
			color: $white;
		}
	}
}

.form {
	@include flex(flex-start,center,wrap);
	&__box {
		width: 100%;
		max-width: 300px;
		padding: 0 15px 15px 15px;
		&--100 {
			max-width: unset;
		}
	}
	&--footer {
		.form__box {
			@media #{$mediaDesktop}{
				max-width: 250px;
			}
			@media #{$mediaTablet}{
				order: 8;
				width: 50%;
				max-width: unset;
				&:nth-of-type(1){
					order: 1;
				}
				&:nth-of-type(2){
					order: 3;
					text-align: center;
				}
				&:nth-of-type(3){
					display: none;
					opacity: 0;
				}
				&:nth-of-type(4){
					order: 2;
				}
				&:nth-of-type(5){
					order: 4;
				}
				&:nth-of-type(6){
					
				}
				&:nth-of-type(7){
					
				}
				&:nth-of-type(8){
					text-align: center;
				}
			}
			@media #{$mediaMobile}{
				width: 100%;
			}
		}
	}
	&__input {
		width: 100%;
		padding: 10px 15px;
		padding-left: 40px;
		border: 1px solid $white;
		border-radius: 2px;
		background: rgba($white,.25);
		font-family: "dinMedium", Arial, Helvetica, sans-serif;
		@include font-size(14);
		color: $white;
		@media #{$mediaMobile}{
			padding-left: 15px;
			text-align: center;
		}
	}
	&__checkbox {
		display: none;
		&:checked + label::before {
			color: $white;
		}
		.miniBox &:checked + label::before {
			color: $black2;
		}
	}
	&__radio {
		display: none;
		&:checked + label::before {
			color: $white;
		}
	}
	&__labelCheckbox {
		@include font-size(11);
		color: $white;
		&::before {
			content: "✔";
			@include flex(center,center,wrap,inline-flex);
			width: 12px;
			height: 12px;
			border: 1px solid $white;
			margin-right: 10px;
			background: transparent;
			color: transparent;
			@include transition;
		}
	}
	&__labelRadio {
		padding-left: 20px;
		margin-right: 20px;
		@include font-size(16);
		font-weight: bold;
		color: $white;
		position: relative;
		&::before {
			content: "᛫";
			@include flex(center,center,wrap,inline-flex);
			width: 12px;
			height: 12px;
			border: 2px solid $white;
			border-radius: 50%;
			margin-top: auto;
			margin-bottom: auto;
			margin-right: 10px;
			background: transparent;
			color: transparent;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			@include transition;
		}
	}
	&--banner {
		@include flex(center,center,wrap);
		padding-top: 10px;
		padding-bottom: 10px;
		@media #{$mediaMobile}{
			max-width: 300px;
		}
		.form__box {
			max-width: 300px;
		}
		.form__input {
			padding-left: 15px;
			border-color: rgba($black,.3);
			background: rgba($black,.2);
			color: $black2;
			text-align: center;
			@include placeholder {
				color: $black2;
			}
		}
		.form__labelRadio, .form__labelCheckbox, .form__labelRadio::before {
			color: $black2;
		}
		.form__labelRadio::before, .form__labelCheckbox::before {
			border-color: $black2;
		}
		.form__labelRadio::before {
			color: transparent;
		}
		.form__radio:checked + label::before {
			color: $black2;
		}
	}
}