.miniBox {
    @include flex(center,center,wrap);
    width: 100%;
    max-width: 350px;
    padding-top: 110px;
    border-radius: 0 0 10px 10px;
    background: $white;
    position: relative;
    &--modal {
        padding-top: 0;
        border-radius: 10px;
        position: relative;
        .miniBox__top {
            width: 100%;
            padding: 20px 10px;
            border-radius: 10px 10px 0 0;
            background: $lBlack;
            color: $white;
        }
        .miniBox__title {
            margin-bottom: 10px;
            @include font-size(18);
            font-weight: bold;
        }
        .miniBox__desc {
            padding: 0;
            margin: 0;
            @include font-size(14);
            font-weight: normal;
        }
        @media #{$mediaMobile}{
            border-radius: 10px !important;
        }
    }
    @media #{$mediaMobile}{
        max-width: unset;
        padding-top: 0;
        border-radius: 0;
    }
    &::after {
        // content: "";
        // display: block;
        // width: 100%;
        // max-width: inherit;
        // height: 100%;
        // border-radius: 0 0 10px 10px;
        // background: rgba($black,.3);
        // position: absolute;
        // top: 7px;
        // left: 7px;
    }
    &__img {
        min-width: 420px;
        position: absolute;
        top: -80px;
        z-index: +1;
        &.mobile {
            display: none;
            position: static;
        }
        @media #{$mediaMobile}{
            display: none;
            &.mobile {
                display: block;
                top: 0;
            }
        }
    }
    &__alert {
        @include flex;
        width: 100%;
        height: 100%;
        padding: 80px 10px 10px;
        border: solid $primary;
        border-width: 0 5px 5px 5px;
        background: $black;
        text-align: center;
        color: $white;
        position: absolute;
        top: 0;
        left: 0;
        @include transition;
        &__text {
            @include font-size(30);
            color: $white;
        }
    }
}

.icons {
    @include flex(space-around,center,wrap);
    padding-top: 80px;
    padding-bottom: 80px;
}

.boxIcon {
    @include flex(center,center,wrap);
    width: 100%;
    max-width: 220px;
    padding: 10px;
    text-align: center;
}

.text {
    @include font-size(16);
    &--m {
        @include font-size(20);
    }
}

.bold {
    font-family: "dinProBlack", Arial, Helvetica, sans-serif;
}

.red {
    color: $primary;
}