.icon {
    display: inline-block;
    width: 40px;
    height: 40px;
    &--clock {
        background: url("../images/icons/icon-clock.png") no-repeat center;
        background-size: contain;
    }
    &--chart {
        background: url("../images/icons/icon-chart.png") no-repeat center;
        background-size: contain;
    }
    &--headphones {
        background: url("../images/icons/icon-headphones.png") no-repeat center;
        background-size: contain;
    }
    &--person {
        background: url("../images/icons/icon-person.png") no-repeat center;
        background-size: contain;
    }
}