.header {
    width: 100%;
    background: $white;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    &__top {
        @include flex;
        min-height: 90px;
        background: $primary;
    }
    &__bottom {
        @include flex;
        min-height: 50px;
        background: $white;
        @media #{$mediaMobile} {
            min-height: unset;
            height: auto;
            .header__container {
                height: auto;
                text-align: center;
            }
        }
    }
    &__container, &__content {
        .header__top & {
            @include flex(center,center,wrap);
            min-height: inherit;
        }
    }
    &__container {
        @media #{$mediaMobile}{
            padding-left: 0;
            padding-right: 0;
        }
    }
    &__content {
        @media #{$mediaMobile}{
            width: 100%;
            min-height: unset !important;
        }
    }
    &__link {
        display: none;
        @media #{$mediaMobile}{
            display: inline-block;
            color: $white;
            text-decoration: underline;
            line-height: 0;
        }
    }
    &__boxes {
        @include flex(center,center,wrap);
        width: 100%;
        max-width: 220px;
        min-height: inherit;
        @media #{$mediaTablet}{
            max-width: 200px;
        }
        @media #{$mediaTablet}{
            max-width: 200px;
        }
        @media #{$mediaMobile}{
            width: 50%;
            max-width: unset;
            min-height: unset;
            height: 60px;
        }
        &--black {
            background: $lBlack;
        }
    }
    &__title, &__text {
        padding-left: 15px;
        padding-right: 15px;
        margin: 0;
        font-family: "dinBlack", Arial, Helvetica, sans-serif;
        @include font-size(18);
        text-align: center;
        color: $white;
        @media #{$mediaTablet}{
            @include font-size(16);
        }
    }
    &__title {
        @media #{$mediaMobile}{
            order: 1;
            width: 100%;
            padding: 5px;
            padding-top: 3px;
            background: $lBlue;
            line-height: 1;
        }
    }
    &__text {
        padding-left: 18px;
        padding-right: 18px;
        font-family: "dinMedium", Arial, Helvetica, sans-serif;
        @media #{$mediaMobile}{
            @include font-size(15);
            padding-left: 10px;
            padding-right: 10px;
        }
        @media #{$mediaXMobile}{
            padding-left: 5px;
            padding-right: 5px;
        }
    }
}

.logo {
    margin-right: auto;
    margin-bottom: 10px;
    line-height: 0;
    @media #{$mediaMobile}{
        width: 125px;
        margin-top: 7px;
        margin-bottom: 7px;
        margin-left: 10px;
    }
}