.footer {
    padding-top: 62px;
    padding-bottom: 42px;
    background: $black2;
    &__container {
        @include flex(center,flex-start,wrap);
    }
    &__title {
        font-family: "dinBlack", Arial, Helvetica, sans-serif;
        text-transform: uppercase;
        text-align: center;
        color: $white;
    }
    &__left {
        width: 180px;
        @media #{$mediaMobile}{
            width: 100%;
        }
    }
    &__right {
        padding-left: 18px;
        width: calc(100% - 180px);
        @media #{$mediaMobile}{
            width: 100%;
        }
    }
}